import {useEffect, useState} from 'react'
import axios from 'axios';

//mui
import Stack from '@mui/material/Stack';
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';
import CheckIcon from '@mui/icons-material/Check';
import ToggleButton from '@mui/material/ToggleButton';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import ClickAwayListener from '@mui/base/ClickAwayListener'; //
import Box from "@mui/material/Box";

//redux store
import { useSelector,useDispatch} from 'react-redux';
import {RootState} from '../../store/store'
import RegisterMenuSlice from '../../store/RegisterMenuSlice';
import RegisterAgreeSlice from '../../store/RegisterAgreeSlice';


export default function UserAgreement(){
    const dispatch = useDispatch()
    const [warning,setWarning] = useState<boolean>(false)
    const [allAgree,setAllAgree] = useState<boolean>(false)// 
    const personalInfoAgree = useSelector<RootState,boolean>(state=>{return state.agreement.personalinfo})//
    const locationInfoAgree = useSelector<RootState,boolean>(state=>{return state.agreement.locationinfo})//
    const receiveAgree = useSelector<RootState,boolean>(state=>{return state.agreement.receive})//

    const handleClickAway = () => {
        setWarning(false);
      };

    const submit = async ()=>{
        if(personalInfoAgree){
            dispatch(RegisterAgreeSlice.actions.personal(true)) //  
            if(locationInfoAgree){dispatch(RegisterAgreeSlice.actions.location(true))} // 
            if(receiveAgree){dispatch(RegisterAgreeSlice.actions.receive(true))} // 
            dispatch(RegisterMenuSlice.actions.inputInfo()) //  
        }else{
            setWarning(true)

        }
    }


    useEffect(()=>{
        if(allAgree){
            dispatch(RegisterAgreeSlice.actions.personal(true))
            dispatch(RegisterAgreeSlice.actions.location(true))
            dispatch(RegisterAgreeSlice.actions.receive(true))
        }else{
            dispatch(RegisterAgreeSlice.actions.personal(false))
            dispatch(RegisterAgreeSlice.actions.location(false))
            dispatch(RegisterAgreeSlice.actions.receive(false))
        }
    },[allAgree])


    return <>
                <ToggleButton
                sx={{mb:1,width:"100%",alignItems:"start",fontWeight:"bold"}}
                    value="check"
                    selected={allAgree}
                    onChange={() => {
                        setAllAgree(!allAgree);
                    }}
                    >
                    <CheckIcon />, ,  
                </ToggleButton>
                <ToggleButton
                    sx={{mb:1,width:"100%"}}
                    value="check"
                    selected={personalInfoAgree}
                    onChange={() => {
                        dispatch(RegisterAgreeSlice.actions.personal(!personalInfoAgree));
                    }}
                    >
                    <CheckIcon />   <Box component="span" fontSize="5px" color="green">()</Box>
                </ToggleButton>
                <ToggleButton
                sx={{mb:1, width:"100%", textAlign:"start"}}
                    value="check"
                    selected={locationInfoAgree}
                    onChange={() => {
                        dispatch(RegisterAgreeSlice.actions.location(!locationInfoAgree));
                    }}
                    >
                    <CheckIcon />   <Box component="span" fontSize="5px" >()</Box>
                </ToggleButton>
                <ToggleButton
                sx={{mb:1,width:"100%"}}
                    value="check"
                    selected={receiveAgree}
                    onChange={() => {
                        dispatch(RegisterAgreeSlice.actions.receive(!receiveAgree));
                    }}
                    >
                    <CheckIcon />  <Box component="span" fontSize="5px" >()</Box>
                </ToggleButton>

                <ButtonGroup sx={{mb:1,width:"100%"}} variant="contained" aria-label="outlined primary button group">
                    <Button sx={{width:"50%"}}></Button>
                    <Button sx={{width:"50%"}} onClick={submit}></Button>
                </ButtonGroup>
                {warning? <ClickAwayListener onClickAway={handleClickAway}>
                        <Stack sx={{ width: '100%' }} spacing={2}>
                            <Alert severity="warning" onClick={()=>{setWarning(false)}}>
                                <AlertTitle></AlertTitle>
                                "    ."
                            </Alert>
                        </Stack>
                    </ClickAwayListener>:<></>}


    </>
}